/**
 * asd
 */
class A {
  someMethod() {
    return true;
  }
}

const a = new A();
console.log(a.someMethod());

(async () => {
  await true;
  console.log('foo');
})();

function* generator(i) {
  yield i;
  yield i + 10;
}
const gen = generator(10);
console.log(gen.next().value);

class People {
  static async *[Symbol.asyncIterator] (){
    await true;
    yield "true";
  }
}
(async ()  => {
  for await (let person of People) {
    console.log(person);
  }
})();